


.container{
    height: 100%;
    width: 100%;
   display: flex;
   flex-direction: row;
}

.infoPanel{
    flex:1;
    padding: 20px;
}

.codePanel{
    flex:1;
    position: relative;
    border-left: 1px solid #ccc;
}

.codePanelInner{
    position: absolute;
    top: 0;
    bottom: 50px;
    width:100%
}

.codePanelEditor{
    width: 100%;
    height: 100%;
}

.codePanelBar{
    height: 50px;
    display: flex;
    width:100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    padding: 0 20px;
    bottom: 0px;
}

.previewPanel{
    width:350px;
    position: relative;
    border-left: 1px solid #cccccc;
}

.previewPanelNav{
    height:45px;
    background-color: #fff;
    box-shadow: 0px 5px 20px 0px rgba(0,87,255,0.1);
    
    .tab{
        display: inline-block;
        height: 45px;
        line-height: 45px;
        padding: 0 20px;
        cursor: pointer;
        i{
            margin-right: 5px;
        }
    }

    .tab.active{
        border-bottom: 2px solid red;
    }
}

.previewPanelInner{
    position: absolute;
    top: 45px;
    bottom: 0px;
    width:100%;

    iframe{
        width: 100%;
        height: 100%;
        border: none;
    }

}
@primary-color: #3860f4;