.header{
    height:60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    z-index: 100;
    position: relative;
    box-shadow: 0px 5px 20px 0px rgba(0,87,255,0.1);
}

.headerTitle{
    font-weight: 600;
    font-size: 20px;
    line-height: 20px;
}

.wrapper{
    height: 100%;
    width: 100%;
}

.main{
    position: fixed;
    top: 60px;
    bottom: 0px;
    width: 100%;
}
@primary-color: #3860f4;